/**
 * Êñá‰ª∂‰∏ä‰º†
 * @param {String} url - ‰∏ä‰º†Êé•Âè£Âú∞ÂùÄ
 * @param {String} filePath - Êú¨Âú∞Êñá‰ª∂Ë∑ØÂæÑ
 * @param {Object} formData - È¢ùÂ§ñÁöÑË°®ÂçïÊï∞ÊçÆ
 * @returns {Promise} ‰∏ä‰º†ÁªìÊûú
 */
export function uploadFile(url, filePath, formData = {}) {
	return new Promise((resolve, reject) => {
		// 1. Ê∑ªÂä†TokenÂà∞ËØ∑Ê±ÇÂ§¥
		const token = getToken()
		const header = {}
		if (token) {
			header['Authorization'] = `Bearer ${token}`
		}

		// 2. Ê∑ªÂä†Âü∫Á°ÄURL
		if (!url.startsWith('http')) {
			url = API_BASE_URL + url
		}

		// 3. ÊâìÂç∞‰∏ä‰º†Êó•Âøó
		console.log('üì§ Êñá‰ª∂‰∏ä‰º†:', {
			url,
			filePath,
			formData
		})

		// 4. ÂèëËµ∑‰∏ä‰º†ËØ∑Ê±Ç
		uni.uploadFile({
			url,
			filePath,
			name: 'file',
			header,
			formData,
			success: (response) => {
				console.log('üì• ‰∏ä‰º†ÂìçÂ∫î:', response)

				try {
					const data = JSON.parse(response.data)

					if (data.code === 200) {
						resolve(data)
					} else {
						uni.showToast({
							title: data.message || '‰∏ä‰º†Â§±Ë¥•',
							icon: 'none'
						})
						reject(new Error(data.message || '‰∏ä‰º†Â§±Ë¥•'))
					}
				} catch (error) {
					console.error('Ëß£ÊûêÂìçÂ∫îÂ§±Ë¥•:', error)
					uni.showToast({
						title: '‰∏ä‰º†Â§±Ë¥•',
						icon: 'none'
					})
					reject(error)
				}
			},
			fail: (error) => {
				console.error('‚ùå ‰∏ä‰º†Â§±Ë¥•:', error)
				uni.showToast({
					title: 'ÁΩëÁªúËøûÊé•Â§±Ë¥•',
					icon: 'none'
				})
				reject(error)
			}
		})
	})
}

// ÂØºÂá∫ÈªòËÆ§ÊñπÊ≥ï
export default request
